.App {
  text-align: center;
}

.App h2 {
  font-family: Now, sans-serif;
  color: white;
  letter-spacing: 2px;
  font-size: 35px;
  font-weight: 100;
}

.App h3 {
  font-family: Now, sans-serif;
  color: white;
  letter-spacing: 2px;
  font-size: 25px;
  font-weight: 100;
}

.App a {
  text-decoration: none;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%)
}
